.title {
  font-size: 48px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -0.004em;
  @include xxl {
    font-size: 32px; }
  @include lm {
    font-size: 28px; }
  @include md {
    font-size: 42px; } }

.title-lg {
  font-size: 62px;
  font-weight: 300;
  line-height: 1.2;
  letter-spacing: -0.002em;
  color: $textBlack;
  @include xxl {
    font-size: 48px; }
  @include lm {
    font-size: 32px; }
  @include md {
    font-size: 55px; }
  @include sm {
    font-size: 44px; } }

.text-light {
  color: $textLight; }

